body {
    width: 1250px;
    margin: 0 auto;
    font-family: Nunito;
}

section#products {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    column-gap: 25px;
    row-gap: 50px;
    height: 1200px;
    padding-left: 50px;
    padding-right: 50px;
    margin-bottom: 100px;
}

.a-container {
    background: white;
    text-decoration: none;
    color: #333333;
    box-shadow: 0 0 5px #33333366;
}

.a-container:visited {
    color: #333333;
}

.a-container:hover {
    color: #F1A0B3;
}

.div-container {
    padding: 30px 20px;
    height: 290px;
    text-align: center;
}

.a-container img {
    height: 228.75px;
    width: 228.75px;
}

.a-container p {
    font-size: 20px;
}