body {
    width: 1250px;
    margin: 0 auto;
    font-family: Nunito, sans-serif;
}

/* Section main */
section#accmain header {
    height: 150px;
    padding-bottom: 75px;
    text-align: center;
}

section#accmain header p {
    color: #333333;
    font-weight: bold;
    font-size: 36px;
    margin: 0;
}

section#accmain header hr {
    border: 0;
    width: 1250px;
    height: 10px;
    position: relative;
    top: 0;
    right: 0;
    background: linear-gradient(to bottom, #F1A0B3, #ffffff);
}

section#accmain header strong {
    color: #F1A0B3;
}

.art-info {
    color: #3E3F43;
    font-size: 36px;
    position: relative;
    top: -100px;
}

.art-input {
    display: grid;
    grid-template-columns: 3fr 4fr;
    position: relative;
    bottom: 50px;
}

.div-input1 {
    padding: 0 50px;
    border-right: dashed 3px #33333366;
    min-height: 100px;
}

.div-input1 input {
    width: 434.917px;
    background: #FFFFFF;
    box-sizing: border-box;
    border: 1px solid #F1A0B3;
    height: 40px;
    margin-top: 25px;
}

.div-input1::placeholder {
    color: #AFAFAF;
    font-family: Nunito, sans-serif;
    font-size: 16px;
    line-height: 22px;
}

.div-input1 input:focus {
    background-color: #f2f2f2;
    outline: none;
}

.div-input2 {
    padding: 0 50px;
    min-height: 100px;
    text-align: center;
}

.div-input2 span {
    font-size: 36px;
    color: #3E3F43;
}

.div-input2 p {
    font-size: 24px;
    color: #3E3F43;
    text-align: left;
}

.button {
    border: 1px solid #F1A0B3;
    height: 40px;
    font-size: 16px;
    color: white;
    background-color: #F1A0B3;
    margin-top: 18px;
    margin-left: 50px;
    margin-right: 50px;
    width: 150px;
    border-radius: 25% 25%;
}

.button:hover {
    background-color: #45a049;
    /* Verde mai închis */
}

/* products.css */
#products {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
    padding: 20px;
}

.div-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #fff;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    transition: transform 0.2s;
}

.div-container:hover {
    transform: scale(1.05);
}

.div-img {
    width: 100%;
    height: auto;
    border-radius: 8px;
}

.div-container p {
    margin: 10px 0;
    color: #3E3F43;
    font-size: 18px;
    text-align: center;
}