.payment-page {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
}

.payment-page h2 {
    text-align: center;
    color: #333;
}

.payment-page form {
    display: flex;
    flex-direction: column;
}

.payment-page form div {
    margin-bottom: 15px;
}

.payment-page form label {
    margin-bottom: 5px;
    font-weight: bold;
}

.payment-page form input,
.payment-page form select {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 100%;
    box-sizing: border-box;
    background-color: #fff;
    transition: border-color 0.3s ease;
}

.payment-page form select:focus,
.payment-page form input:focus {
    border-color: #4CAF50;
    outline: none;
}

/* Stil pentru butonul de plată */
.payment-page form button {
    padding: 10px;
    background-color: #4CAF50;
    color: white;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.payment-page form button:hover {
    background-color: #45a049;
}