.shopping-cart ul {
    list-style-type: none;
    padding: 0;
}

.cart-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    margin-bottom: 20px;
    border-bottom: 1px solid #ddd;
}

.cart-item-img {
    width: 100px;
    height: auto;
    margin-right: 20px;
}

.cart-item-details {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: center;
}

.product-description {
    font-weight: bold;
    margin-bottom: 5px;
}

.product-price {
    color: #333;
}

.quantity-controls {
    display: flex;
    align-items: center;
}

.quantity-btn {
    background-color: #F29FB3;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    margin: 0 10px;
    font-size: 16px;
    border-radius: 50%;
}

.quantity-btn:hover {
    background-color: #fffbfb;
}

.quantity {
    font-size: 16px;
    padding: 5px;
}

.remove-button {
    background-color: #ff4d4d;
    color: white;
    border: none;
    padding: 8px 16px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.remove-button:hover {
    background-color: #ff1a1a;
}

.cart-total {
    font-size: 1.5em;
    margin-top: 20px;
    text-align: right;
}


.payment-button {
    background-color: #F29FB3;
    /* Verde */
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.payment-button:hover {
    background-color: #dbafdf;
    /* Schimbare la hover */
}

.payment-button:active {
    background-color: #3e8e41;
    /* Schimbare la click */
    transform: scale(0.98);
    /* Ușoară micșorare la click */
}